}

.barra__logo img {
    width: auto;
    mix-blend-mode: multiply
}

.barra__logo span {
    float: right;
    height: 30px;
    line-height: 27px
}

.hamburger {
    height: 45px;
    width: 45px;
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    background: #262626
}

.hamburger span {
    height: 2px;
    border-radius: 10px;
    background-color: white
}

.hamburger span:nth-child(1) {
    position: absolute;
    top: 12px;
    left: 12px;
    transition: all 0.5s cubic-bezier(0.23, 1, 0.32, 1) 0s;
    width: 18px
}

.hamburger span:nth-child(2) {
    position: absolute;
    top: 21px;
    left: 12px;
    transition: all 0.5s cubic-bezier(0.23, 1, 0.32, 1) 0s;
    width: 18px
}

.hamburger span:nth-child(3) {
    position: absolute;
    top: 30px;
    left: 12px;
    transition: all 0.5s cubic-bezier(0.23, 1, 0.32, 1) 0s;
    width: 13px
}

.hamburger:hover span:nth-child(1) {
    width: 16px;
    transform: rotate(-45deg);
    top: 25px;
    left: 20px
}

.hamburger:hover span:nth-child(2) {
    width: 21px;
    transform: rotate(90deg)
}

.hamburger:hover span:nth-child(3) {
    width: 16px;
    transform: rotate(45deg);
    left: 9px;
    top: 25px
}

.close {
    display: block;
    visibility: visible;
}

.close span:nth-child(1) {
    width: 26px;
    transform: rotate(45deg);
    top: 22px;
    left: 10px
}

.close span:nth-child(2) {
    width: 0;
    opacity: 0;
    top: 15px
}

.close span:nth-child(3) {
    width: 26px;
    transform: rotate(-45deg);
    top: 22px;
    left: 10px
}

.close:hover span:nth-child(1) {
    width: 16px;
    transform: rotate(-45deg);
    top: 15px;
    left: 11px
}

.close:hover span:nth-child(2) {
    width: 24px;
    opacity: 1;
    top: 21px;
    transform: rotate(90deg)
}

.close:hover span:nth-child(3) {
    width: 16px;
    transform: rotate(45deg);
    top: 15px;
    left: 21px
}

