.texto__encabezado--titulo {
    opacity: 0;
    pointer-events: none;
    position: absolute;
    display: inline-block;
    max-width: 700px;
    width: 100%;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    left: -50px;
    
}

.barra__texto.change .texto__encabezado {
    opacity: 0;
    transition: all cubic-bezier(0.51, 0.07, 0.47, 0.91) 0.4s;
    pointer-events: none;
    position: absolute
}

.barra__texto.change .texto__encabezado--titulo {
    opacity: 1;
    pointer-events: auto;
    transition: all cubic-bezier(0.51, 0.07, 0.47, 0.91) 0.6s;
    position: absolute;
    left: 100px
}


