.menu {
    position: fixed;
   z-index: 999999999;
    overflow-y: auto;
    top:0;
    left: 0;
    width:100%;
    height: 100vh;
    pointer-events: none;
    color: #FFF;
    font-family: "Roboto_condensed";
    transition: all cubic-bezier(0.51, 0.07, 0.47, 0.91) 0.4s;
    &.open2 {
        pointer-events: auto;
        background:rgba(0,0,0,.5);
        .menu-nav {
            transform:translate3d(0,0,0);
        }
    }
    .menu-nav {
        position: relative;
        display: flex;
        height: 100%;
        align-items: center;
        background-color:#216087;
        width:100%;
        max-width: 370px;
        transform:translate3d(-100%,0,0);
        padding: 20px;
        transition: all cubic-bezier(0.51, 0.07, 0.47, 0.91) 0.4s;
    }
    .close {
        position: absolute;
        top:0;
        right: 0;
        left: auto;
    }

    ul {
        margin: auto;
        li {
            a {
                display: block;
                padding: 5px 0;
                color: #FFF;
                font-size: calc(16px + .4vw);
                line-height: 160%;
                transition: all cubic-bezier(0.51, 0.07, 0.47, 0.91) 0.4s;
                &:hover {
                    opacity: .5;
                }
            }
        }
    }
}




