.progress {
  z-index: 10;
  width: 100%;
  height: 0.3em;
  margin-bottom: 0px;
  position: fixed;
  top: 45px;
  overflow: hidden;
  background-color: #F0EDEC;
  content: "";
  display: table;
  table-layout: fixed;
}

.progress-bar {
  position:absolute;
  width: 0%;
  float: left;
  height: 100%;
  z-index: 99;
  max-width: 100%;
  background-color: #2e6d9d;
  -webkit-transition: width cubic-bezier(0.51, 0.07, 0.47, 0.91) 0.5s;
  -o-transition: width cubic-bezier(0.51, 0.07, 0.47, 0.91) 0.5s;
  transition: width cubic-bezier(0.51, 0.07, 0.47, 0.91) 0.5s;
}

.hidden-content {
  position: absolute !important;
  height: 1px;
  width: 1px;
  overflow: hidden;
  clip: rect(1px, 1px, 1px, 1px);
  clip-path: polygon(0 0, 0 0, 0 0, 0 0);
}
