@import url("https://fonts.googleapis.com/css?family=Vidaloka");
@import url("https://fonts.googleapis.com/css?family=Fira+Sans+Condensed:300,400,500,600,700");

.expansion .barra-menu.fixed-menu .barra__logo {
    top:2px;
}
body.expansion {
    background: #fff1e0;
    }

.expansion .barra-menu {
    &.fixed-menu {
        .expansion {
            display: block;
        }
    }
}

.expansion .cabecera h1 {
    font-family: "Vidaloka","Times New Roman",Georgia,serif;
    font-weight: normal;
}

.expansion .cuerpo p, .expansion .cuerpo ul {
    font-family: "Fira Sans Condensed",Arial,sans-serif,helvetica;
}

.expansion .ue-l-cover-grid__inner, .expansion main {
    background: #fff1e0;
}

.expansion .ue-c-cover-content__headline-group, .expansion .cuerpo h2 {
    font-family: "Vidaloka","Times New Roman",Georgia,serif;
    font-weight: normal;
    text-transform:none;
}

.expansion .ue-c-cover-content__byline-item {
    float: left;
    font-family: "Vidaloka","Times New Roman",Georgia,serif;
    margin-right: 7px;
    font-size: 12px;
    letter-spacing: 0.15em;
    color: #5c5851;
    text-transform: uppercase;
    font-style: normal;

}

.expansion .barra__logo {
    display: flex;
    align-items: center;
    min-width: 120px;
    left: 0;
    top: 5px;
    .expansion {
        position: relative;
        display: none;
        padding-right: 15px;
        &:before {
            content: '';
            position: absolute;
            top:0;
            right:0;
            width:1px;
            height: 100%;
            background: #ccc;
        }
    }
    img {
        width:100%;
    }
    a {
        display: block;
        max-width: 100px;
        margin-right: 15px;
        &.expansion {
            max-width: 120px;
        }
    }
}

.expansion .ue-c-cover-content__kicker {
    color: #315b97;
    position: relative;
    display: block;
    font-family: "Fira Sans Condensed",Arial,sans-serif,helvetica;
    font-weight: 600;
    text-transform: uppercase;
    font-size: 17px;
    margin: 0 0 5px;
}

.expansion .ue-c-cover-content__link {
       color: #181715;
}

.expansion .ue-c-cover-content {
    width: 100%;
    box-sizing: border-box;
    position: relative;
    display: flex;
    flex-direction: column;
    font-variant-ligatures: common-ligatures;
    border-bottom: 1px solid #e2d8cb;
    background: #fff1e0;
}

.expansion .ue-c-cover-content__main {
    box-sizing: border-box;
    width: 100%;
    display: flex;
    flex-direction: column;
    flex: 1 1 auto;
    border-top: none;
    padding: .625rem .625rem 0 0;
}

.expansion .ue-c-cover-content__standfirst {
    font-family: "Fira Sans Condensed",Arial,sans-serif,helvetica;
    font-weight: 300;
}

.margen {margin-left: 10px;}

.expansion .ue-c-cover-content--media-right-from-tablet.has-image .ue-c-cover-content__main {
    border-top-style: none;
}

.expansion .ue-c-cover-content__link:hover, .ue-c-cover-content__link:hover .ue-c-cover-content__kicker {
    color: #386ab0;
}

.expansion .progress-bar {
    position: absolute;
    width: 0%;
    float: left;
    height: 100%;
    z-index: 99;
    max-width: 100%;
    background-color: #386ab0;
    -webkit-transition: width cubic-bezier(0.51, 0.07, 0.47, 0.91) 0.5s;
    transition: width cubic-bezier(0.51, 0.07, 0.47, 0.91) 0.5s;
}

.expansion .footer a {
    color: #386ab0;
    text-decoration: none;
}

.expansion .footer a:hover {
    opacity: .8;
}

.expansion .footer a:visited {
    color: #386ab0;
    text-decoration: none;
    font-size: 14px;
}


.expansion .barra-menu {
    background-color:#FFF;
}

.expansion .site-header.main-section-header.inside .nav-container {
       display: none;
}

.expansion .site-header.main-section-header .container-header {
    display: none;
}

.expansion header .header-wrapper {
    position: relative;
    max-width: 1280px;
    margin: 0 auto;
    overflow: hidden;
    display: none;
}

.expansion header .header-wrapper {
    display: block;
}

.expansion header .header-wrapper  {
    border-bottom: 8px solid #386ab0;
}

.expansion .site-header.main-section-header {
    background-color: transparent;
}

.expansion header .header-wrapper .header-logo {
    height: 49px;
    z-index: 1;
    padding-top: 0;
}

.expansion .progress {
    opacity: 0;
    &.show {
        opacity: 1;
    }
    &.hidden {
        opacity: 0;
    }
}

.expansion .header-subscribe {
    position: absolute;
    top:7px;
    right: 0;
    a {
        background: #386ab0;
        color: #fff;
        border-radius: 4px;
        padding: 10px;
        color: #FFF;
        font-family: "Fira Sans Condensed",Arial,sans-serif,helvetica;
        font-size: 16px;
        line-height: 100%;
        font-weight: 300;
        text-decoration: none;
        position: relative;
        display: inline-block;
        cursor: pointer;
        transition: background-color 200ms ease-out 0s;
    }
}

.expansion header .header-wrapper  .header-logo {
    float: left;
    margin: 0 15px 0 0;
}

.expansion header .header-wrapper .header-logo a {
    background: url(../images/expansion.svg) no-repeat transparent 0 50%/120px 49px;
    height: 49px;
    width: 120px;
    display: inline-block;
    text-indent: -99999px;
}

.expansion .close, .expansion .hidden-content, .expansion .no-visible {
    display: none;
    visibility: hidden;
}

.hidden-content {
    position: absolute !important;
    height: 1px;
    width: 1px;
    overflow: hidden;
    clip: rect(1px, 1px, 1px, 1px);
    clip-path: polygon(0 0, 0 0, 0 0, 0 0);
}

.expansion header .header-wrapper .header-links {
    position: relative;
    float: left;
}

.expansion header .header-wrapper .header-links li {
    display: inline-block;
}

.expansion header .header-wrapper .header-links li:hover {
    
    background: #efedec;
}

.expansion header .header-wrapper .header-links a {
    display: inline-block;
    position: relative;
    padding: 14px 10px 14px;
    font-family: "Fira Sans Condensed",Arial,sans-serif,helvetica;
    font-size: 16px;
    color: #000;
    text-decoration: none;
}

.expansion header .header-wrapper .header-links a:hover {
    color: #2e6d9d;
}


@media screen and (max-width: 739px) {
    .expansion header .header-wrapper .header-links {
        display: none;
    }
	.expansion .ue-c-cover-content__main{padding: .625rem .625rem 0;}
	.margen{margin-left: 0;}
	
}




 

