@media (min-width: 321px) {
  .header-container {
    width: 100%;
  }
}

@media (min-width: 815px) and (max-width: 1023px) {
  .header-container {
    width: 100%;
  }
}

@media (min-width: 1024px) {
  .header-container {
    width: 1280px;
  }
}

@media (max-width: 1280px) {
  .header-container {
    max-width: 990px;
  }
}

@media screen and (max-width: 1044px) {
  .cabecera {
    margin-right: 15px;
    width: calc(100% - 194px);
  }

  .cuerpo {
    margin-right: 15px;
    width: calc(100% - 194px);
  }

  .ue-l-cover-grid__inner aside {
    margin-left: 15px;
  }

  .site-footer {
    margin: 0 15px;
  }
}

@media screen and (max-width: 865px) {
  .menu-hidden {
    display: block;
  }
}

@media screen and (max-width: 765px) {
  .cuerpo blockquote {
    padding: 3rem;
  }

  .cuerpo blockquote p {
    font-size: 21px;
    line-height: 24px;
  }
}

@media screen and (max-width: 739px) {
  .expansion header .header-wrapper .header-links {
    display: none;
  }

  .expansion .ue-c-cover-content__main {
    padding: .625rem .625rem 0;
  }

  .margen {
    margin-left: 0;
  }
}

@media screen and (max-width: 656px) {
  .site-header.main-section-header.inside .nav-tabs {
    display: none;
  }

  .cabecera {
    margin-right: 0px;
    width: 100%;
    float: none;
  }

  .cuerpo {
    margin-right: 0px;
    width: 100%;
    float: none;
  }

  .ue-l-cover-grid__inner aside {
    width: 100%;
    display: block;
    box-sizing: border-box;
    padding-left: 0;
    padding-right: 0;
    position: relative;
    margin: 20px auto 0;
  }

  .ue-l-cover-grid__inner aside .autor p {
    display: block;
  }

  .ue-l-cover-grid__inner aside .autor .mas-info:hover + .mas-info-expand {
    height: 70px;
  }

  .ue-l-cover-grid__inner aside .autor .mas-info {
    left: 92px;
  }

  .header .header-wrapper .header-title {
    font-size: 14px;
    margin: 12px 0 0 0;
  }

  .header.close .header-wrapper .header-title {
    margin: 12px 0 0 0;
  }
}

@media screen and (max-width: 590px) {
  .site-footer .footer-dos .contenido-footer-dos {
    flex-direction: column;
  }

  .site-footer .footer-dos .copyright {
    width: 100%;
  }

  .site-footer .footer-dos .contenido-footer-dos .partners {
    width: 100%;
    display: flex;
    justify-content: center;
  }

  .site-footer .footer-dos .rrss-footer ul {
    justify-content: center;
  }
}

@media screen and (max-width: 525px) {
  .cabecera h1 {
    font-size: 32px;
    line-height: 38px;
  }
}

@media screen and (max-width: 510px) {
  .cuerpo blockquote {
    width: 100%;
    float: none;
    margin: 20px auto;
  }

  .cuerpo .img-left {
    width: 100%;
    float: none;
    margin: 20px auto;
  }

  .cuerpo .img-right {
    width: 100%;
    float: none;
    margin: 20px auto;
  }
}

@media screen and (max-width: 460px) {
  .site-footer .footer-uno .lista-otras-webs {
    flex-wrap: wrap;
  }
}

@media screen and (max-width: 440px) {
  .ue-l-cover-grid__inner aside .autor .mas-info:hover + .mas-info-expand {
    height: 85px;
  }
}

@media screen and (max-width: 330px) {
  .site-footer .footer-uno .lista-otras-webs {
    justify-content: center;
    flex-direction: column;
  }

  .lista-otras-webs .list-footer li {
    text-align: center;
  }

  .site-footer .footer-tres .contenido-footer-tres ul {
    flex-direction: column;
  }

  .site-footer .footer-tres .contenido-footer-tres ul li a:after {
    display: none;
  }
}

@media (max-width: 320px) {
  .header-container {
    width: 100%;
  }
}
